@use 'parts/breakpoints' as bp;

@use 'helpers/reset.scss';
@use 'helpers/container.scss';

:root {
  //----- Colors -----//
  --c-background-100: #ffffff;
  --c-background-400: #f0f3f7;

  --c-on-background-400: #5c7899;
  --c-on-background-800: #0075ff;

  --c-accent-400: #ff5c00;
  --c-accent-500: #d75205;

  --c-on-accent-400: #ffffff;

  --c-secondary-400: #0075ff;
  --c-secondary-600: #204166;

  --c-on-secondary-400: #ffffff;

  //----- Content Width -----//
  --content-max-width: 700px;
  --content-breakout-max-width: 1200px;

  //----- Spacings -----//
  --s-siblings: 1rem;
  --s-siblings-small: 0.6rem;
  --s-siblings-medium: 2rem;
  --s-siblings-large: 6rem;
  --s-between-sections-gap-vertical: 4rem;

  --s-sides-inset: 2rem;

  //----- Other -----//
  font-family: Inter Variable, Inter, system-ui, Avenir, Helvetica, Arial,
    sans-serif;
  line-height: 1.5;
  font-weight: 400;

  color-scheme: light;
  color: var(--c-on-background-400);
  background-color: var(--c-background-400);

  font-synthesis: none;
  text-rendering: optimizeLegibility;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  color: var(--c-on-background-400);
  background-color: var(--c-background-400);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  color: var(--c-on-background-800);
}

h2 {
  font-size: 2.2rem;
  line-height: 1.2;
  font-weight: 600;

  @include bp.md {
    font-size: 2.5rem;
  }
}

.navbar {
  background-color: var(--c-background-100);

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 0;
  }

  &__logo {
    a {
      text-decoration: none;
    }
  }
}

.free-section {
  display: flex;
  gap: 1rem;
  flex-direction: column;
  padding: 5rem 0;

  &__title {
    margin: 0 0 0.5rem 0;
  }

  @include bp.sm {
    text-align: center;
    padding: 10rem 0;

    &__title {
      margin: 0 0 1rem 0;
    }
  }
}

.snapping-section {
  scroll-snap-align: start;
  background-color: var(--c-secondary-400);
  color: var(--c-on-secondary-400);

  &__inner {
    display: grid;
    grid-template-columns: 1fr;
    align-items: center;

    gap: var(--s-siblings-medium);
    padding: var(--s-between-sections-gap-vertical) 0;
  }

  &__text-box {
    display: flex;
    gap: 1rem;
    flex-direction: column;
  }

  &__title {
    color: var(--c-on-secondary-400);

    font-size: 2.1rem;

    @include bp.md {
      font-size: 2.3rem;
    }
  }

  img {
    width: 100%;
    height: auto;
  }

  @include bp.md {
    &__inner {
      min-height: 100svh;
      gap: var(--s-siblings-large);
      grid-template-columns: 1fr 1fr;
    }

    img {
      flex-shrink: 0;
      flex-basis: 50%;
      width: 100%;
      height: auto;
    }
  }
}

.footer {
  background-color: var(--c-secondary-600);
  color: var(--c-on-secondary-400);
  font-size: 0.9em;

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1.5rem 0;
  }

  &__link {
    color: var(--c-on-secondary-400);
    transition: color 0.25s;

    &:hover,
    &:focus-visible {
      color: var(--c-accent-400);
    }
  }
}

.button-link {
  text-decoration: none;

  border-radius: 8px;
  padding: 0.8em 1.2em;
  font-size: 1em;
  font-weight: 600;
  font-family: inherit;
  background-color: var(--c-accent-400);
  color: var(--c-on-accent-400);
  cursor: pointer;
  transition: background-color 0.25s;

  &:hover,
  &:focus-visible {
    background-color: var(--c-accent-500);
  }

  @include bp.md {
    padding: 0.6em 1.2em;
  }
}

//Gives us a background colour that matches the navbar and the footer when user overscrolls on devices like the iPhone
body:after {
  content: '';

  position: fixed;
  z-index: -1;
  bottom: -50%;
  left: -50%;
  top: -50%;
  right: -50%;

  // background-color: red;

  background-size: cover;
  background-position: -50% -50%;

  background-image: -moz-linear-gradient(
    var(--c-background-100) 50%,
    var(--c-secondary-600) 50%
  );

  background-image: -webkit-linear-gradient(
    var(--c-background-100) 50%,
    var(--c-secondary-600) 50%
  );
  background-image: -o-linear-gradient(
    var(--c-background-100) 50%,
    var(--c-secondary-600) 50%
  );
  background-image: -ms-linear-gradient(
    var(--c-background-100) 50%,
    var(--c-secondary-600) 50%
  );
  background-image: linear-gradient(
    var(--c-background-100) 50%,
    var(--c-secondary-600) 50%
  );
}
